<template>
    <div class="hello">
        <h2 style="text-align: center">{{ msg }}</h2>
        <van-dropdown-menu active-color='#3dbbb7' :duration='0.25'>
            <van-dropdown-item v-model="value1" :options="this.$store.state.eastbayList" />
        </van-dropdown-menu>
        <van-index-bar highlight-color="#3dbbb7" :index-list="[]">
            <div v-for="(goods, index) in this.$store.state.eastbaygoodsList" :key="index" :index="index">
                <van-index-anchor>{{index}}</van-index-anchor>
                <van-image width="72" height="72" v-for="(item, key,index) in goods" :key="key" :src="'https://images.final-score.com/pi/'+item.sku+'/large/'+item.sku+'.jpeg'" @click="imgclick(goods,index)" />
            </div>
        </van-index-bar>
    </div>
</template>

<script>
    import {
        ImagePreview
    } from 'vant';
    export default {
        data() {
            return {
                msg: "设置",
                loading: false,
                value1: 0
            };
        },
        mounted() {},
        methods: {
            imgclick(goods, index) {
                var urls = []
                for (var sku in goods) {
                    var url = 'https://images.final-score.com/pi/' + sku + '/large/' + sku + '.jpeg?wid=300&hei=300&fmt=png-alpha'
                    urls.push(url)
                }
                ImagePreview({
                    images: urls,
                    startPosition: index,
                    showIndex:false,
                    showIndicators:true,
                });
            }
        }
    };
</script>

<style scoped>

</style>



