<template>
    <div class="hello">
        <h2 style="text-align: center">{{ msg }}</h2>
        <van-list finished finished-text="没有更多了">
            <a v-for="goods  in this.$store.state.goodsList" :key="goods.id" :href="goods.producturl" target="_Blank">
                <van-card :title="goods.name" :desc="goods.product" :thumb="goods.productimg"></van-card>
            </a>
        </van-list>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                msg: "上新通知",
            }
        },
        methods: {}
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



