<template>
    <div id="app">
        <router-view></router-view>
        <van-tabbar v-model="active" active-color="#3dbbb7" route @change='tabbarChange'>
            <van-tabbar-item replace v-for="(tabbar, index) in tabbars" :key="index" :to='tabbar.link' :icon='tabbar.icon' :dot='tabbar.num==-1' :info='tabbar.num==-1?"":tabbar.num'>{{tabbar.title}}</van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                msg: "测试界面",
                active: 0,
                tabbars: [{ //num等于空不作显示，-1显示点，其余显示数字
                    'icon': 'home-o',
                    'title': '主页',
                    'num': null,
                    'link': '/newSize'
                }, {
                    'icon': 'notes-o',
                    'title': '上新',
                    'num': null,
                    'link': '/newProduct'
                }, {
                    'icon': 'contact',
                    'title': '我的',
                    'num': null,
                    'link': '/personal'
                }, {
                    'icon': 'setting-o',
                    'title': '设置',
                    'num': null,
                    'link': '/setting'
                }]
            };
        },
        created() {
            this.$MQTT.setCallback(this.MQTTcallback)
            this.$api
                .get("api/products/search?products=&query=jordan%20aj%201%3Arelevance%3AproductType%3A200005&currentPage=1&pageSize=200&timestamp=1", {})
                .then(res => {
                    res.data.products.forEach((item, i) => {
                        var time = {
                            text: item.name,
                            value: item.baseProduct
                        }
                        if (this.$store.state.eastbaygoodsList[item.name] == null) {
                            this.$store.state.eastbaygoodsList[item.name] = {}
                        }
                        item.variantOptions.forEach((t) => {
                            if (this.$store.state.eastbaygoodsList[item.name][t.sku] == null) {
                                this.$store.state.eastbaygoodsList[item.name][t.sku] = t
                            }
                        });
                        this.$store.state.eastbayList.splice(0, 0, time);
                    });
                    var time = {
                        text: "全部商品",
                        value: 0
                    }
                    this.$store.state.eastbayList.splice(0, 0, time);
                    // this.$store.state.eastbayList  = res.data.products
                })
            this.$api
                .get("product/jordan-why-not-zero-1-low-mens/Q9682100.html", {})
                .then(res => {
                    var JSONstring = res.data.split('window.footlocker.STATE_FROM_SERVER = ')[1].split('window.digitalData')[0].replace('};','}')
                    var obj = JSON.parse(JSONstring); //由JSON字符串转换为JSON对象
                    console.log(obj.details)
                });
        },
        methods: {
            tabbarChange() {
                let time = this.tabbars[this.active]
                time.num = null
                this.$set(this.tabbars, time, this.active)
            },
            MQTTcallback(res) {
                console.log(res)
                var json = JSON.parse(res.payload.payloadString);
                // let time = this.tabbars[0]
                switch (res.devId) {
                    case "newSize":
                        for (var item of json) {
                            this.$store.state.newSizeList.splice(0, 0, item);
                        }
                        // time.num = -1
                        // this.$set(this.tabbars, time, 0)
                        break;
                    case "newProducts":
                        for (var item of json) {
                            this.$store.state.goodsList.splice(0, 0, item);
                        }
                        // time = this.tabbars[1]
                        // time.num = -1
                        // this.$set(this.tabbars, time, 1)
                        break;
                }
            }
        }
    };
</script>

<style>
    #app {
        font-family: "Avenir", Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #3dbbb7;
    }
    h1,
    h2,
    h3 {
        font-weight: normal;
    }
</style>



