<template>
    <div class="hello">
        <h2 style="text-align: center">{{ msg }}</h2>
        <van-list v-model="loading" finished finished-text="没有更多了">
            <a v-for="goods  in this.$store.state.newSizeList" :key="goods.id" :href="goods.url" target="_Blank">
                <van-card :title="goods.sku+ '  '+goods.name " :desc="goods.newSize" :thumb="goods.img">
                </van-card>
            </a>
        </van-list>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                msg: "补货通知",
                loading: false,
            };
        },
        methods: {}
    };
</script>

<style scoped>

</style>



