//导入 mqtt.js
import Vue from 'vue'
import "@/lib/mqttws31.js";
import UsrCloud from "@/lib/usrCloud.js";
var userMqtt = new UsrCloud.UsrCloud();
//定义回调
var callbacklist = [];

/**
* 连接丢失回调（云组态和云交换机共用）
*/
function USR_onConnLost(event) {
  console.log(event);
}


var callback = {
  USR_onConnAck: function (res) {
    userMqtt.USR_Subscribe("/#", "/public/admin/lzy");
    console.log("连接成功")
  },
  USR_onConnLost: USR_onConnLost,

  USR_onRcvRawFromDev: function (res) {
    for (var i = 0; i < callbacklist.length; i++) {
      
      callbacklist[i](res);
    }
  },
};


userMqtt.Usr_Init("mq.tongxinmao.com", 18832, 2, callback);
userMqtt.USR_Connect("", "", new Date().getTime() * Math.random());

userMqtt.setCallback = function (callback) {
  callbacklist.push(callback)
}

Vue.prototype.$MQTT = userMqtt; 


